@import './var.scss';

.home-scan-hero {
  margin-right: 20px;
  padding-top: 40px;
  width: 540px;
  @include desktop-only {
    width: min(540px, 90vw);
  }

  .color-green {
    color: #78ffc7;
  }

  .tab-header {
    position: relative;
    display: flex;
    .tab-title {
      position: relative;
      flex: 1;
      padding: 11px 0;
      background: #3e404c;
      border-radius: 10px 10px 0px 0px;
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.3);
      line-height: 28px;
      text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
      text-align: center;
      cursor: pointer;
      user-select: none;
    }
    .tab-active {
      background: #383a47;
      color: rgba(255, 255, 255, 1);
      &:after {
        content: '';
        position: absolute;
        bottom: 4px;
        left: 50%;
        width: 30px;
        height: 4px;
        border-radius: 2px;
        background: linear-gradient(to right, rgba(148, 130, 248, 1) 0%, rgba(230, 77, 255, 1) 100%);
        transform: translateX(-50%);
      }
    }
  }
  .tab-main {
    min-height: 460px;
    background: #383a47;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1), 0px 0px 6px 0px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 10px 10px;
    color: #fff;
  }

  .info-chart-box {
    .info-chart-title {
      padding: 0 20px;
      font-size: 30px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 42px;
      text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
      .rate {
        margin-left: 10px;
        font-size: 15px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        line-height: 21px;
      }
    }
  }
  .hide-component {
    display: none;
  }

  .home-price {
    position: relative;
    .scan-loading {
      z-index: 999;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(0, 0, 0, 0.65);
    }
    .home-price-header {
      display: flex;
      align-items: center;
      .token-header {
        flex: 1;
      }
    }
    .scan-time-picker {
      display: flex;
      margin-right: 14px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 5px;
      color: rgba(255, 255, 255, 0.3);
      .scan-picker-item {
        padding: 4px 0;
        width: 55px;
        font-size: 12px;
        line-height: 17px;
        border-radius: 5px;
        text-align: center;
        border-radius: 5px;
        cursor: pointer;
        user-select: none;
      }
      .scan-picker-active {
        background: rgba(148, 130, 248, 1);
        color: #fff;
      }
    }

    .home-price-detail {
      padding: 0 14px;
      .home-price-title {
        display: flex;
        word-break: break-all;
        .title-left {
          flex: 1;
        }
        .sum {
          margin-right: 10px;
          font-size: 30px;
          line-height: 42px;
        }
        .desc1 {
          font-size: 15px;
          line-height: 21px;
          // color: #78ffc7;
        }
        .time {
          padding-bottom: 6px;
          align-self: end;
          margin-left: 10px;
          font-size: 12px;
          line-height: 17px;
          color: rgba(255, 255, 255, 0.3);
        }
      }

      .home-price-sub {
        display: flex;
        flex-wrap: wrap;
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 12px;
        .row {
          margin-right: 10px;
          &:last-child {
            margin-right: 0;
          }
          .label {
            color: #fff;
          }
        }
      }
    }
  }
  // .scan-hero-pc {
  //   margin-right: 20px;
  //   padding-top: 40px;
  //   width: 540px;
  //   @include desktop-only {
  //     width: min(540px, 90vw);
  //   }
  // }

  .hide-for-pc {
    @extend %hide-mobile;
  }

  .hide-for-mobile {
    @extend %hide-desktop;
  }
}

@include mobile-only {
  .home-scan-hero {
    margin-right: 0;
    padding-top: 0;
    width: 100%;
    .home-price {
      .scan-time-picker {
        display: flex;
        margin: 0;
      }
      .title-left {
        display: block;
      }
      .price-time-box {
        display: flex;
        align-items: flex-end;
        margin: 12px 0;
        .time {
          margin-left: 10px;
          flex: 1;
          font-size: 12px;
          line-height: 17px;
          color: rgba(255, 255, 255, 0.3);
        }
      }

      .home-price-detail {
        padding: 0 14px;
        .home-price-title {
          .sum {
            margin-right: 10px;
            font-size: 30px;
            line-height: 42px;
          }
          .desc1 {
            font-size: 15px;
            line-height: 21px;
          }
        }

        .home-price-sub {
          display: block;
          font-size: 10px;
          line-height: 1.2;
          .row {
            display: inline-block;
            margin-right: 12px;
            margin-bottom: 8px;
            &:last-child {
              margin-right: 0;
            }
          }
        }
      }
    }
  }
}

.scan-hero-drawer {
  @extend %hide-desktop;
  .ant-drawer-content {
    background: rgba(56, 58, 71, 1);
    border-radius: 10px 10px 0px 0px;
  }
  .ant-drawer-body {
    padding: 0;
  }
  .close {
    position: absolute;
    right: 22px;
    top: 20px;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.5;
    img {
      width: 14px;
      height: 14px;
    }
  }
  .tab-header {
    .tab-title {
      flex: 0 1 auto;
      width: auto;
      background: transparent;
      padding: 20px 15px 15px;
      font-size: 15px;
      line-height: 21px;
    }
    .tab-active {
      color: rgba(255, 255, 255, 1);
      &:after {
        content: '';
        position: absolute;
        bottom: 10px;
        width: 20px;
        height: 4px;
        border-radius: 2px;
      }
    }
  }

  .tab-main {
    // max-height: 87vh;
    // overflow-y: scroll;
    height: auto;
    background: #383a47;
    box-shadow: none;
    border-radius: 0;
    color: #fff;
  }

  .info-chart-box {
    .info-chart-title {
      padding: 0 15px;
      font-size: 24px;
      line-height: 33px;
      .rate {
        margin-left: 8px;
        font-size: 12px;
        line-height: 17px;
      }
    }
  }
}
