@import './var.scss';

.token-header {
  display: flex;
  align-items: center;
  padding: 20px 16px;
  .icon {
    margin-left: 4px;
    width: 20px;
    height: 20px;
    &:first-child {
      margin-left: 0;
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .name {
    margin: 0 8px;
    font-size: 20px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #ffffff;
    line-height: 28px;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  .switch {
    width: 16px;
    height: 16px;
    img {
      display: block;
      width: 100%;
      height: 100%;
      cursor: pointer;
    }
  }
}

@include mobile-only {
  .token-header {
    padding: 15px;
    .icon {
      margin-left: 4px;
      width: 15px;
      height: 15px;
      &:first-child {
        margin-left: 0;
      }
    }
    .name {
      margin: 0 5px;
      font-size: 15px;
      font-weight: 400;
      color: #ffffff;
      line-height: 21px;
    }
    .switch {
      margin-left: 7px;
      width: 16px;
      height: 16px;
    }
  }
}
