@import './var.scss';
$blue: #5915e1;
$hoverBlue: #3940b7;
$purple: rgba(72, 80, 229, 0.2);
$hoverPurple: #bdc0f5;
$bgColor: #140736;

#root .base-scan-table {
  overflow: hidden;
  background: #252536;
  .tokenSymbol {
    img {
      width: 16px;
      vertical-align: middle;
      border-radius: 50%;
      border: 1px solid #fff;
    }

    .pairIndex {
      margin-right: 10px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 21px;
      color: rgba(255, 255, 255, 0.3);
      width: 20px;
    }

    .tokens {
      position: relative;
      top: -1px;

      &.color-border {
        span {
          border-radius: 100%;
          width: 22px;
          height: 22px;
          display: inline-block;

          img {
            border-radius: 100%;
          }
        }
      }

      .tokens-logo2 {
        margin-left: -5px;
      }
    }

    .token-names {
      margin-left: 10px;
      flex: 1;
      vertical-align: middle;
      color: #9482f8;
    }
  }

  .ant-table {
    border-radius: 0;
    td,
    th {
      flex: 1;
    }
    .ant-table-measure-row {
      display: none;
    }
    .ant-table-container,
    .ant-table-content {
      border-radius: 0;
    }
    .ant-table-placeholder {
      td {
        flex: 1 !important;
        text-align: center !important;
      }
    }
  }

  .ant-table table thead {
    border: 0 !important;
  }

  .ant-table .ant-table-thead > tr > th {
    padding: 12px 0 !important;
    color: #fff;
    background: #2b2b3c !important;
    border-radius: 0 !important;
    &:first-child {
      flex: 0 1 auto;
      padding-left: 40px !important;
      text-indent: 27px;
      background: #2b2b3c !important;
    }
  }

  .ant-table .ant-table-tbody {
    tr > td {
      &:first-child {
        flex: 0 1 auto;
        padding-left: 40px !important;
        background: #252536;
      }
    }
    .ant-table-cell-fix-left {
      background: transparent;
    }
  }

  .ant-table .ant-table-tbody tr td {
    color: #fff !important;
    padding: 20px 5px !important;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
  }

  .ant-table .ant-table-tbody > tr:hover > td {
    background: #272738 !important;
  }

  .ant-table-column-sorters {
    padding: 0;
  }

  .ant-table-thead th.ant-table-column-sort {
    background: transparent;
  }

  td.ant-table-column-sort {
    background: transparent;
  }

  .ant-table-column-sorter-up.active,
  .ant-table-column-sorter-down.active {
    color: $blue;
  }

  .ant-table-pagination.ant-pagination {
    margin-top: 0;
    margin-bottom: 0;
    border-top: none;
    padding: 30px;
  }

  .ant-table-pagination.ant-pagination {
    & > .ant-pagination-item {
      margin-left: 8px;
    }

    .ant-pagination-item-1 {
      margin-left: 0;
    }
  }

  .ant-pagination.mini .ant-pagination-item {
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: rgba(255, 255, 255, 0.3);
    overflow: hidden;
  }

  .ant-pagination-item a {
    color: rgba(255, 255, 255, 0.3);
  }

  .ant-pagination-item-active {
    background: #6d00f4;

    border-color: rgba(109, 0, 244, 1);
    a {
      color: rgba(255, 255, 255, 1);
    }
  }

  .action-title {
    white-space: nowrap;
  }

  .ant-pagination .ant-pagination-disabled {
    opacity: 0.25;
  }

  .btn-list {
    display: flex;
    .btn-link-item {
      margin-right: 10px;
      padding: 4px 13px;
      min-width: 110px;
      font-size: 15px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #5a14e1;
      line-height: 21px;
      border-radius: 4px;
      white-space: nowrap;
      background: #decdff;
      text-align: center;
      cursor: pointer;
      &:hover {
        background: #bdc0f5;
      }
      &:last-child {
        margin-right: 0;
      }
    }
    .btn-link-trade {
      background: #6210ff;
      color: #fff;
      &:hover {
        background: #4711b4;
      }
    }
  }
}

#root .popular-info {
  .ant-table .ant-table-thead > tr > th {
    &:first-child {
      width: 200px;
    }
  }

  .ant-table .ant-table-tbody {
    tr > td {
      &:first-child {
        width: 200px;
      }
    }
  }
}

#root .pair-info {
  .ant-table .ant-table-thead > tr > th {
    &:first-child {
      width: 226px;
    }
  }
  .ant-table .ant-table-tbody {
    tr > td {
      &:first-child {
        width: 226px;
      }
    }
  }
}

#root .complex-table {
  .ant-table .ant-table-thead > tr > th {
    &:last-child {
      flex: 0 1 auto;
      width: 150px;
      padding-left: 20px !important;
      padding-right: 20px !important;
    }
  }
  .ant-table .ant-table-tbody {
    tr > td {
      &:last-child {
        flex: 0 1 auto;
        width: 150px;
        padding-left: 20px !important;
        padding-right: 20px !important;
      }
    }
  }
}

@include desktop-only {
  .mobile {
    display: none;
  }
}

@include mobile-only {
  .pc {
    display: none;
  }
  .mobile {
    display: block;
  }

  #root .base-scan-table {
    .tokenSymbol {
      img {
        width: 14px;
      }

      .pairIndex {
        margin-right: 5px;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        width: 19px;
      }

      .tokens {
        &.color-border {
          span {
            width: 22px;
            height: 22px;
            display: inline-block;
          }
        }

        .tokens-logo2 {
          margin-left: -3px;
        }
      }

      .token-names {
        margin-left: 5px;
        font-size: 14px;
      }
    }
    .ant-table .ant-table-thead > tr > th {
      padding: 10px 0 !important;
      &:first-child {
        flex: 0 1 auto;
        padding-left: 10px !important;
      }
    }

    .ant-table .ant-table-tbody {
      tr > td {
        &:first-child {
          flex: 0 1 auto;
          padding-left: 10px !important;
        }
      }
    }

    .ant-table .ant-table-tbody tr td {
      padding: 10px 2px !important;
    }

    .ant-table-pagination.ant-pagination {
      padding: 20px;
    }

    .btn-list {
      .btn-link-item {
        margin-right: 5px;
        padding: 2px 5px;
        min-width: 50px;
        font-size: 14px;
      }
    }
  }

  #root .table-br {
    .ant-table .ant-table-tbody tr td {
      border-bottom: 0 !important;
    }
  }

  #root .popular-info {
    .ant-table .ant-table-thead > tr > th {
      &:first-child {
        width: 150px;
      }
    }
    .ant-table .ant-table-tbody {
      tr > td {
        &:first-child {
          width: 150px;
        }
      }
    }
  }

  #root .pair-info {
    .ant-table .ant-table-thead > tr > th {
      &:first-child {
        width: 185px;
      }
    }
    .ant-table .ant-table-tbody {
      tr > td {
        &:first-child {
          width: 185px;
        }
      }
    }
  }

  #root .complex-table {
    .ant-table .ant-table-thead > tr > th {
      &:last-child {
        flex: 0 1 auto;
        width: 70px;
        padding-left: 10px !important;
        padding-right: 10px !important;
      }
    }
    .ant-table .ant-table-tbody {
      tr > td {
        &:last-child {
          flex: 0 1 auto;
          width: 70px;
          padding-left: 10px !important;
          padding-right: 10px !important;
        }
      }
    }
  }
}
