@import './var.scss';
.home-scan-main {
  @extend %layout;

  .slogan {
    .title {
      @extend %gradient-title;
      color: #fff;
      font-weight: 600;
      font-size: 32px;
      line-height: 45px;
      text-align: center;
      max-width: 833px;
      margin: 0 auto;

      @include mobile-only {
        font-weight: 600;
        font-size: 18px;
        line-height: 25px;
      }

      @include small-desktop-only {
        max-width: 43.3854vw;
        font-size: 2em;
        line-height: 140.625%;
      }
    }

    .content {
      @extend %dim-content;
      margin: 0 auto;
      margin-top: 8px;
      margin-bottom: 60px;
      text-align: center;
      line-height: 32px;

      &.en {
        max-width: 689px;

        @include small-desktop-only {
          max-width: 35.8854vw;
        }

        @include mobile-only {
          max-width: 455px;
        }
      }

      @include mobile-only {
        margin-bottom: 24px;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
      }

      @include small-desktop-only {
        font-size: 1.125em;
        line-height: 200%;
        margin-top: 0.4444em;
        margin-bottom: 3.3333em;
      }
    }
  }

  .scan-box {
    background: #252536;
    border-radius: 20px;

    .ant-tabs-nav {
      margin: 0 0 10px 0;
      font-weight: 600;
    }
    .ant-tabs-nav-wrap {
      padding: 10px 40px;
      .ant-tabs-ink-bar {
        height: 4px;
        background: transparent;
        &:after {
          content: '';
          position: absolute;
          left: 50%;
          top: 0;
          width: 30px;
          height: 100%;
          background: linear-gradient(90deg, #9482f8 0%, #e64dff 100%);
          border-radius: 2px;
          transform: translateX(-50%);
        }
      }
      .ant-tabs-tab {
        padding: 10px 0;
        font-size: 20px;
        line-height: 28px;
        color: rgba(255, 255, 255, 0.3);
        &.ant-tabs-tab-active {
          .ant-tabs-tab-btn {
            color: #fff;
          }
        }
        & + .ant-tabs-tab {
          margin: 0 0 0 30px;
        }
        .ant-tabs-tab-btn {
          &:active {
            color: #fff;
          }
        }
      }
    }
  }

  .line-type-area {
    .header {
      display: flex;
      align-items: center;
      padding: 0 40px 20px;
      .total {
        margin-right: 10px;
        font-size: 30px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 42px;
      }
      .rate {
        font-size: 15px;
        font-family: PingFangSC-Semibold, PingFang SC;
        line-height: 21px;
      }
      .header-l {
        flex: 1;
      }
    }
  }
}

// PC适配
@include desktop-only {
  .mobile {
    display: none;
  }
}

// 移动端适配
@include mobile-only {
  .pc {
    display: none;
  }
  .home-scan-main {
    margin-left: 0;
    margin-right: 0;
    .scan-box {
      border-radius: 12px;

      .ant-tabs-nav {
        margin: 0;
        .ant-tabs-ink-bar {
          bottom: 5px;
        }
      }
      .ant-tabs-nav-wrap {
        padding: 0px 10px;
        .ant-tabs-tab {
          padding: 12px 0;
          font-size: 15px;
          line-height: 21px;
        }
        .ant-tabs-ink-bar {
          &:after {
            width: 20px;
          }
        }
      }
    }

    .line-type-area {
      .header {
        display: block;
        padding: 10px 10px;
        .total {
          margin-right: 5px;
          font-size: 24px;
          line-height: 33px;
        }
        .rate {
          font-size: 12px;
          line-height: 17px;
        }
      }
    }
  }
}

// 小屏适配
@include small-desktop-only {
  .home-scan-main {
    .title {
      max-width: 43.3854vw;
      font-size: 2em;
      line-height: 140.625%;
    }
  }
}
