.ant-tooltip-inner {
  width: fit-content;
  padding: 14px 20px;
  border-radius: 5px;
  .tr-content li {
    width: 450px;
  }
}

.trade-router-modal {
  // transform: scale(1) !important;

  .ant-modal-content {
    padding: 10px !important;
    border-radius: 10px !important;
  }

  .ant-modal-title {
    text-align: left !important;
    font-size: 15px !important;
  }
  .ant-modal-close {
    width: 20px;
    height: 20px;
    color: #111111;
    opacity: 0.5;
    top: 10px !important;
    right: 10px !important;
    .ant-modal-close-x .anticon {
      background-size: 13px 13px !important;
    }
  }
}

.tr-content {
  color: #2c3039;
  .tr-content-title {
    font-size: 20px;
    font-weight: 600;
    color: #2c3039;
  }
  .token {
    margin-right: 4px;
  }

  > p {
    display: flex;
    align-items: center;
    font-family: Avenir Next;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
    margin-bottom: 16px;

    > img {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }
  }

  > div {
    min-width: 160px;
  }

  ul {
    margin-bottom: 0;
  }

  li {
    margin-top: 8px;
    background: rgba(17, 17, 17, 0.3);
    border-radius: 5px;
    border: 1px solid rgba(17, 17, 17, 0.3);
    padding: 10px 12px;
    background: #ffffff;
    border-radius: 5px;
    color: #2c3039;
    position: relative;
    .tr-arrow {
      display: inline-block;
      width: 13px;
      height: 10px;
      background: url(../images/homeTokens/arrow_default.png) 0 0 no-repeat;
    }
    &.active {
      background: #e6d9ff;
      border-color: #630aea;
      color: #630aea;
      &::after {
        content: '';
        position: absolute;
        top: -1px;
        right: -1px;
        width: 30px;
        height: 30px;
        background: url(../images/homeTokens/router-path-checked.svg) 0 0 no-repeat;
      }
      .path-pool {
        background-color: #630aea !important;
      }
    }
    &:hover {
      background: rgba(99, 10, 234, 0.05);
    }

    .tr-content-li-top {
      margin-bottom: 10px;
      > span {
        font-size: 15px;
        font-weight: 400;
        opacity: 0.5;
      }
      .tr-content-li-title {
        font-size: 15px;
        font-weight: 600;
        margin-right: 10px;
        opacity: 1;
      }
    }

    .tr-content-li-down {
      display: flex;
      flex-flow: wrap;
      font-size: 15px;
      font-weight: 400;
      // width: max-content;

      .tr-content-li-pair {
        display: flex;
        align-items: center;
        margin-right: 8px;
        .tr-content-li-pair-op {
          display: flex;
          align-items: center;
          margin-right: 4px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-size: 12px;
        }
        img {
          width: 15px;
          height: 15px;
          margin-right: 6px;
        }
        &:last-child {
          margin-right: 0;

          .tr-arrow {
            display: none;
          }
        }
        .path-pool {
          background: #2c3039;
          border-radius: 9px;
          opacity: 0.3;
          text-align: center;
          width: fit-content;
          line-height: 15px;
          padding: 0 3px 0 7px;
          .tr-pool {
            margin-right: 5px;
          }
          > span {
            font-size: 12px;
            font-weight: 400;
            color: #ffffff;
          }
        }
      }
    }
  }
}
