@import './var.scss';

#root {
  .trade-modal {
    background: #383a47;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 16px 0 28px;
    height: 680px;
    margin: 40px 20px 15px 0;
    width: 540px;

    @include desktop-only {
      width: min(540px, 90vw);
    }

    .trade-modal-title {
      font-size: 27px;
      font-weight: 600;
      color: #ffffff;
      line-height: 38px;
      text-align: center;
      margin-bottom: 20px;
    }

    .ant-table {
      .ant-table-thead {
        background: #1e1e2f;
        border-bottom: none !important;
        height: 50px;

        > tr > th {
          color: #fff !important;
          background: #1e1e2f !important;
          height: 50px;
          line-height: 1;
        }
      }
      .ant-table-tbody {
        .ant-table-row {
          border-bottom: solid 1px rgba(255, 255, 255, 0.1);

          &:hover {
            .ant-table-cell {
              background-color: #383a47 !important;
              color: #fff !important;
            }
          }
          .ant-table-cell {
            color: #fff !important;
            padding: 20px;
          }
        }
        .ant-table-placeholder {
          .ant-table-cell {
            color: #fff !important;
            text-align: center !important;
            padding: 20px;
          }
        }
      }
    }
    .ant-pagination {
      justify-content: center;
      button {
        color: #e6d9ff !important;
        background: transparent !important;
        min-width: 18px !important;
        margin: 0;
        padding: 0;
        width: 18px;
        height: 18px;
        line-height: 18px;
        span {
          margin: 0;
        }
      }
      .ant-pagination-prev,
      .ant-pagination-next {
        background: transparent !important;
        min-width: 18px !important;
        line-height: 18px;
        height: 18px;
      }
      & > .ant-pagination-item {
        margin-left: 8px;
      }
      .ant-pagination-item-1 {
        margin-left: 0;
      }
      .ant-pagination-item {
        width: 18px;
        height: 18px;
        line-height: 18px;
        font-size: 12px;
        min-width: auto;
        border-radius: 5px;
        border: 1px solid rgba(255, 255, 255, 0.3);
        background: transparent;
        > a {
          font-size: 12px;
          margin: 0;
          padding: 0;
          color: #fff;
          opacity: 0.3;
        }
      }
      .ant-pagination-item-active {
        background: #6d00f4;
        border-radius: 5px;
        border: 1px solid rgba(230, 217, 255, 0.3);

        a {
          font-weight: 600;
          line-height: 17px;
          opacity: 1;
        }
      }
    }
  }
  .active {
    display: block;
  }
}

#root .trade-modal .ant-table .ant-table-tbody > tr:hover:not(.ant-table-expanded-row) > td {
  background-color: #383a47 !important;
}

.trade-drawer {
  .ant-drawer-title {
    text-align: center;
    font-size: 20px;
  }
  .ant-drawer-body {
    padding: 12px 0;
  }
  .list-title {
    color: #fff;
    display: flex;
    font-weight: 600;
    padding: 10px 0 10px 15px;
    background: #1e1e2f;
    > div {
      width: 50%;
    }
  }
  .trade-list-item {
    padding: 12px 15px;
    border-bottom: solid 1px rgba(255, 255, 255, 0.1);
    .list-item-title {
      color: #fff;
      font-size: 12px;
      font-weight: 400;
    }
    .description-outer {
      color: #fff;
    }
    .description-line {
      display: flex;
      > div {
        width: 50%;
      }
    }
    .icon-line {
      .icon-line-text {
        opacity: 0.5;
      }
    }
  }
  .no-data-text {
    padding: 20px 0;
    color: #fff;
    text-align: center;
  }
}

.symbol-icon {
  width: 15px;
  height: 15px;
  margin-right: 2px;
  vertical-align: middle;
}
